<template>
    <div class="guide box">
        <!-- <h3>Guide - Guide - Guide</h3> -->
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
            <van-swipe-item v-for="(img,i) in imgs" :key="i">
                <img v-lazy="img" @click="gotoMain(i)" :src="img" alt="err" class="g-img">
            </van-swipe-item>
        </van-swipe>
    </div>
    
</template>

<style lang="scss" scoped>
    h3{
        font-size: 37.5px;
    }
    .my-swipe,.g-img{
        width: 100%;
        height: 100%;
    }
   
</style>

<script>
import img4 from "@/assets/images/slide4.png"
export default {
    data(){
        return{
            imgs:[//引入assets静态文件，必须模块化引入
                require("@/assets/images/slide1.png"),
                require("@/assets/images/slide2.png"),
                require("@/assets/images/slide3.png"),
                img4,
                
            ]
        }
    },
    methods:{
        gotoMain(i){
            if(i == this.imgs.length -1){
                localStorage.visitCount++;
                this.$router.push({name:"movie"})
            }
            
        }
    },
    mounted(){
        if(localStorage.visitCount){ 
            this.$router.push({name:"movie"})
        }else{
            // localStorage.setItem("visitCount",1)
            localStorage.visitCount = 0
        }
    }
}
</script>